import { Component, OnInit, OnDestroy } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { ActivatedRoute, ParamMap } from '@angular/router';

import { Post } from '../post.model';
import { PostServiceComponent } from '../post.service';
import { AuthServiceComponent } from 'src/app/auth/auth.service';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-post-create',
  templateUrl: './post-create.component.html',
  styleUrls: ['./post-create.component.css']
})
export class PostCreateComponent implements OnInit, OnDestroy {
  private userisAuthenticated: boolean = false;
  private userAuthSubscription: Subscription;
  post: Post;
  isLoading = false;
  form: FormGroup;

  constructor(public postService: PostServiceComponent, public route: ActivatedRoute, public authService: AuthServiceComponent) { }

  ngOnInit() {
    this.form = new FormGroup({
      'task': new FormControl(null, {validators: [Validators.required, Validators.minLength(3)]}),
    });

    this.userisAuthenticated = this.authService.getStatus();
    this.userAuthSubscription = this.authService.getAuthStatus()
      .subscribe(isAuthenticated => {
        this.userisAuthenticated = isAuthenticated;
      });

    // this.route.paramMap.subscribe((paramMap: ParamMap) => {
    //   if (paramMap.has('postId')) {
    //     this.mode = 'edit';
    //     this.postId = paramMap.get('postId');
    //     this.isLoading = true;
    //     this.postService.getPost(this.postId)
    //       .subscribe((postData) => {
    //         this.isLoading = false;
    //         this.post = {
    //           id: postData._id,
    //           task: postData.title,
    //           creator: postData.creator
    //         };
    //         this.form.setValue({
    //           'task': this.post.task,
    //         });
    //       });
    //   } else {
    //     this.mode = 'create';
    //     this.postId = null;
    //   }
    // });
  }

  onAddPost() {
    this.isLoading = true;
    if (this.form.invalid) {
      return;
    }
    console.log(this.form.value.task);
    this.postService.addPost(this.form.value.task);
    this.form.reset();
  }

  ngOnDestroy() {
    this.userAuthSubscription.unsubscribe();
  }

}
